import { elementsValueList } from '../elements-value-list';

class NewElement {
  constructor() {
    this.name = '';
  }

  get fullName() {
    return this._getFullName();
  }

  _getFullName() {
    return '';
  }

  _getName(element) {
    let name = '';
    element.forEach((item) => {
      if (item === '') {
        name += '';
      } else {
        name += item;
      }
    });
    return name;
  }

  getScores() {
    const name = this.fullName.toLowerCase();

    if (elementsValueList[name]) {
      return elementsValueList[name];
    } else {
      return 0;
    }
  }
}

export class NewElementJump extends NewElement {
  constructor() {
    super();
    this.rotations = '';
    // TODO = проверку на nul;
    this.underrotate = '';
    this.edge = '';
  }

  _getFullName() {
    return this._getName([
      this.rotations,
      this.name,
      this.underrotate,
      this.edge,
    ]);
  }
}

class NewElementSpin extends NewElement {
  constructor() {
    super();
    this.fly = false;
    this.change = false;
    this.level = '';
    this.v = false;
  }

  convertPropertyToString(property, value) {
    return property ? value : '';
  }

  _getFullName() {
    return this._getName([
      this.convertPropertyToString(this.fly, 'F'),
      this.convertPropertyToString(this.change, 'C'),
      this.name,
      this.level,
      this.convertPropertyToString(this.v, 'V'),
    ]);
  }
}

class NewElementStep extends NewElement {
  constructor() {
    super();
    this.level = '';
  }

  _getFullName() {
    return this._getName([this.name, this.level]);
  }
}

function createNewJump() {
  return new NewElementJump();
}

function createNewSpin() {
  return new NewElementSpin();
}

function createNewStep() {
  return new NewElementStep();
}

export { createNewJump, createNewSpin, createNewStep };
